.app-Wrapper {
	min-height: 100vh;
	display: flex;
	align-items: center;
	position: fixed;
	height: 100%;
	width: 100%;
	justify-content: center;
	@media screen and (max-width: 767px) {
		flex-direction: column;
		background-color: #222;
	}
}
.app-Logo {
	position: fixed;
	top: 16px;
	left: 16px;
	z-index: 9;
}
.app-Wrapper_Stream {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
	height: 100vh;
	width: calc(100vh * (640/480));
	@media screen and (max-width: 767px) {
		width: 100vw;
		height: calc(100vw * 0.75);
	}
}
.app-Wrapper_StreamLocal {
	position: absolute;
	max-width: 740px;
	@media screen and (max-width: 767px) {
		width: 100%;
	}
}
.app-Wrapper_StreamLocal-fixed {
	top: 8px;
	right: 8px;
	max-width: 150px;
	z-index: 1;
	height: auto;
	@media screen and (max-width: 767px) {
		max-width: 100px;
	}
}
.app-Wrapper_StreamLocalWrapper {
	position: relative;
	border-radius: var(--border-radius);
	overflow: hidden;
	display: flex;
	height: 100%;
}
.app-Wrapper_StreamLocalWrapperActions {
	position: absolute;
	bottom: 24px;
	width: 100%;
	text-align: center;
	button {
		margin: 0 8px;
	}
	@media screen and (max-width: 767px) {
		position: fixed;
	}
}
.app-Wrapper_StreamRemote {
	position: x;
	width: 100%;
	background-color: #f5f5f5;
	text-align: center;
	.sw-Camera {
			height: 100vh;
			width: calc(100vh * (640/480));
			@media screen and (max-width: 767px) {
				width: 100vw;
				height: calc(100vw * 0.75);
			}
	}
}
.app-Wrapper_StreamRemoteWaiting {
	position: relative;
	height: 100vh;
	width: calc(100vh * (640/480));
	@media screen and (max-width: 767px) {
		width: 100vw;
		height: calc(100vw * 0.75);
	}
}
.app-Wrapper_StreamRemoteWaitingContent {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}
.app-MakeCall {
	text-align: center;
	padding: 16px;
}
.app-Wrapper_Actions {
	min-width: 300px;
	text-align: center;
	img {
		margin-bottom: 16px;
	}
}
